.video {
  position: relative;         /* Con la posición relativa en el vídeo, significa que sus demás elementos no pueden salir de él*/
  max-height: 60rem;          /* Tu altura máxima es 90rem. Después de eso, no crece más */ 
  overflow: hidden;

  .overlay {
    position: absolute;       /* Con la posición absoluta en el overlay, significa que no puede salir del vídeo pero posicionarse encima de él */
  width: 100%;              /* El overlay toma todo el espacio del video */ 
    height: 100%;
  /* Gradiente hecho en ColorZilla */
    /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#bc0000+0,7f00ba+57,1e69de+100&0.54+0,0.5+100 */
    background: -moz-linear-gradient(-45deg,  rgba(188,0,0,0.54) 0%, rgba(127,0,186,0.52) 57%, rgba(30,105,222,0.5) 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(-45deg,  rgba(188,0,0,0.54) 0%,rgba(127,0,186,0.52) 57%,rgba(30,105,222,0.5) 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(135deg,  rgba(188,0,0,0.54) 0%,rgba(127,0,186,0.52) 57%,rgba(30,105,222,0.5) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#8abc0000', endColorstr='#801e69de',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}

  video {
    display: block;           /* Por default, los vídeos tienen un display inline y dejan una línea abajo pequeña que se quita cambiando a block */
    width: 100%;              /* Para que tome todo el espacio posible */
  }
}

.contenido-video {
  display: flex;
  align-items: center;        /* Alinear los items al centro con flexbox */
  height: 100%;               /* Que centre en todo el espacio el vídeo */

  h2 {
    display: flex;            /* Habilitamos flexbox */ 
    flex-direction: column;   /* Salto de línea con el span del index.html */
    align-items: flex-start;  /* Los items se alinean a la izquierda y así el span no depende tanto del tamaño del h2 */
    color: $blanco;
    font-size: 5rem;
  }

  span {
    background-color: $rosa;
    font-size: 2rem;
    padding: 1rem 2rem;       /* Agrandando el background del "span" */
    display: flex;
  }
}
