.lineup {
  background-color: $rosa;
  padding: $separacion 0; 

  h3, p {
    text-align: center;
    color: $blanco;
  }

  .dia {
    text-transform: uppercase;
    margin: $separacion 0;
  }
}

.escenarios-contenedor {
  @include tablet {
    @include grid(2,$separacion);
  }
  
  .escenario {                    
    padding-top: 2rem;          // Añadiendo separacion superior
  }

  .nombre-escenario {
    color: $negro;
    font-size: 2rem;
    font-weight: $delgada; 

    // (!) EL SIGUIENTE CÓDIGO ES PARA DAR FORMATO A LA IMAGEN, TANTO PARA LA DE ROCK Y LA EDM
    &:after {                     // After es porque va después del "nombre.escenario". El & es para que vaya junto
      content: '';                // Sin el content, no hay contenido y no aparecería en la página
      width: 20rem;               // Ancho del contenido
      height: 10rem;              // Alto del contenido
      display: block;             // Dando display de block
      margin: 2rem auto 0 auto;   // Para centrar el content
      background-size: 15rem;      // Dandole tamaño al background (la imagen) para que se vea mejor
      background-repeat: no-repeat;         // Por default los background se repiten, así le quitamos esa propiedad
      background-position: center center;   // Centrando arriba y abajo e izquierda y derecha
    }
  }
}

.edm .nombre-escenario {
  &:after {
    background-image: url('../img/icono_edm.png')
  }
}

.rock .nombre-escenario {
  &:after {
    background-image: url('../img/icono_rock.png')
  }
}

.calendario {
  padding: 0;
  list-style: none;

  li {
    padding: 2.5rem;
    font-weight: $bold;

    span {
    font-weight: $regular;
  }

  }
  
}

.bg-verde .calendario li:nth-child(odd) {         // Le aplica el siguiente código a los nones
  background-color: darken($verde,10)                // Coloca el fondo del color "$verde" más oscuro con una intensidad de 10
}

.bg-amarillo .calendario li:nth-child(odd) {         // Le aplica el siguiente código a los nones
  background-color: darken($amarillo,10)                // Coloca el fondo del color "$verde" más oscuro con una intensidad de 10
}
