@mixin telefono {                   /* Declarando un Mixin */ 
  @media (min-width: $telefono) {       /* Creando un mediaquery para los teléfonos celulares */
    @content;                       /* Le pasamos el @content para que todos los valores escritos dentro de las {  }se pasen aquí */
  }
}

@mixin tablet {
  @media (min-width: $tablet) {
    @content;
  }
}

@mixin desktop {
  @media (min-width: $desktop) {
    @content;
  }
}

@mixin grid($columnas,$espaciado) {              /* Mixin para hacer "tarjetas" de grid  con un número de columnas que nosotros mandemos */
  display: grid;
  grid-template-columns: repeat($columnas,1fr);
  gap: $espaciado;                              /* Separación del grid */
}
/* Se puede usar estos mixins para los demás proyectos */
