.informacion {
  padding: $separacion 0;
  @include tablet {                   /* Usando el mixin (como una función) para las tablets */
  @include grid(2,$separacion);                 /* Usando mixin grid */
  }
  .imagen-informacion {
    margin-bottom: 2rem;              /* Margen abajo de la imagen */
  }
}

.contenido-informacion {
  display: flex;                    /* Display de flex */
  flex-direction: column;           /* Colocamos la dirección el flex en columas (por defaul, es en filas) */
  justify-content: center;              /* Centramos, pero como está en columnas, se centra verticalmente (arriba y abajo) */

  h2 {
    font-size: 3rem;
  }

  .fecha {
    color: $rosa;
    font-weight: $bold;             /* Tenemos una variable para hacer la fuente bold */
  } 
  
  .texto {
    line-height: 1.5;               /* Interlineado */ 
  }
}
