.galeria {
  padding: $separacion 0;

  h3 {
    text-align: center;
  }

}

.galeria-imagenes {
  padding: 0;
  list-style: none;

  @include grid(2,$separacion);         // Incluyendo el grid con el mixin

  @include tablet {
    @include grid(4, 2.5rem);
  }

  @include desktop {
    @include grid(6, 2.5rem)
  }
}

.overlay {
  position: fixed;                      // Deja una posición fija y la imagen no se mueve
  background-color: rgba(0,0,0,.5);     // Para agregar un color negro pero transparente
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;                  // Opción para centrar tanto horizontal como verticalmente
}

.btn-cerrar {
  width: 3rem;
  height: 3rem;
  background-color: $blanco;
  display: grid;
  place-items: center;
  font-weight: $black;
  font-size: 2rem;
  border-radius: 50%;                 // Para hacer un círculo

  &:hover {
    cursor: pointer;
  }
}
