/* Se puede usar estos mixins para los demás proyectos */
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijar-body {
  overflow: hidden;
}

p {
  font-size: 2rem;
  color: #000;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
  font-weight: 900;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.bg-verde {
  background-color: #4CB880;
}

.bg-amarillo {
  background-color: #fef46d;
}

.header {
  background-color: #4CB880;
}
.header .contenido-header {
  /* En SASS se pone el contenido dentro del header porque forma parte de él */
  padding: 2.5rem 0;
}
@media (min-width: 768px) {
  .header .contenido-header {
    /* Llamar al mixin */
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header h1 {
  text-align: center;
  color: #fff;
}
@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0;
  }
}
.header.fijo {
  /* Cuando el header tenga la clase .fijo también... */
  position: fixed;
  width: 100%;
  /* Que tome todo el espacio disponible */
  box-shadow: 0 0.3rem 0.4rem #444;
  /* Toma los sig valores para tener una sombra: sombra en el eje de las x, sombra en las y, radio de la sombra, color*/
}

.navegacion-principal {
  display: flex;
  /* Para agregar separación */
  gap: 2rem;
  /* De 2 rem, le ponemos un display de flex y el gap */
  justify-content: center;
}
.navegacion-principal a {
  color: #fff;
  font-size: 2rem;
}
.navegacion-principal a:hover {
  /* Para que, al pasar el mouse por los enlaces, cambie a amarillo */
  color: #fef46d;
  /* El & es para hacer referencia al enlace "a" */
}

.video {
  position: relative;
  /* Con la posición relativa en el vídeo, significa que sus demás elementos no pueden salir de él*/
  max-height: 60rem;
  /* Tu altura máxima es 90rem. Después de eso, no crece más */
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  /* Con la posición absoluta en el overlay, significa que no puede salir del vídeo pero posicionarse encima de él */
  width: 100%;
  /* El overlay toma todo el espacio del video */
  height: 100%;
  /* Gradiente hecho en ColorZilla */
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#bc0000+0,7f00ba+57,1e69de+100&0.54+0,0.5+100 */
  background: -moz-linear-gradient(-45deg, rgba(188, 0, 0, 0.54) 0%, rgba(127, 0, 186, 0.52) 57%, rgba(30, 105, 222, 0.5) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(-45deg, rgba(188, 0, 0, 0.54) 0%, rgba(127, 0, 186, 0.52) 57%, rgba(30, 105, 222, 0.5) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(135deg, rgba(188, 0, 0, 0.54) 0%, rgba(127, 0, 186, 0.52) 57%, rgba(30, 105, 222, 0.5) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#8abc0000", endColorstr="#801e69de",GradientType=1 );
  /* IE6-9 fallback on horizontal gradient */
}
.video video {
  display: block;
  /* Por default, los vídeos tienen un display inline y dejan una línea abajo pequeña que se quita cambiando a block */
  width: 100%;
  /* Para que tome todo el espacio posible */
}

.contenido-video {
  display: flex;
  align-items: center;
  /* Alinear los items al centro con flexbox */
  height: 100%;
  /* Que centre en todo el espacio el vídeo */
}
.contenido-video h2 {
  display: flex;
  /* Habilitamos flexbox */
  flex-direction: column;
  /* Salto de línea con el span del index.html */
  align-items: flex-start;
  /* Los items se alinean a la izquierda y así el span no depende tanto del tamaño del h2 */
  color: #fff;
  font-size: 5rem;
}
.contenido-video span {
  background-color: #f53756;
  font-size: 2rem;
  padding: 1rem 2rem;
  /* Agrandando el background del "span" */
  display: flex;
}

.informacion {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .informacion {
    /* Usando el mixin (como una función) para las tablets */
    /* Mixin para hacer "tarjetas" de grid  con un número de columnas que nosotros mandemos */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    /* Separación del grid */
    /* Usando mixin grid */
  }
}
.informacion .imagen-informacion {
  margin-bottom: 2rem;
  /* Margen abajo de la imagen */
}

.contenido-informacion {
  display: flex;
  /* Display de flex */
  flex-direction: column;
  /* Colocamos la dirección el flex en columas (por defaul, es en filas) */
  justify-content: center;
  /* Centramos, pero como está en columnas, se centra verticalmente (arriba y abajo) */
}
.contenido-informacion h2 {
  font-size: 3rem;
}
.contenido-informacion .fecha {
  color: #f53756;
  font-weight: 700;
  /* Tenemos una variable para hacer la fuente bold */
}
.contenido-informacion .texto {
  line-height: 1.5;
  /* Interlineado */
}

.lineup {
  background-color: #f53756;
  padding: 5rem 0;
}
.lineup h3, .lineup p {
  text-align: center;
  color: #fff;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 5rem 0;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    /* Mixin para hacer "tarjetas" de grid  con un número de columnas que nosotros mandemos */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    /* Separación del grid */
  }
}
.escenarios-contenedor .escenario {
  padding-top: 2rem;
}
.escenarios-contenedor .nombre-escenario {
  color: #000;
  font-size: 2rem;
  font-weight: 300;
}
.escenarios-contenedor .nombre-escenario:after {
  content: "";
  width: 20rem;
  height: 10rem;
  display: block;
  margin: 2rem auto 0 auto;
  background-size: 15rem;
  background-repeat: no-repeat;
  background-position: center center;
}

.edm .nombre-escenario:after {
  background-image: url("../img/icono_edm.png");
}

.rock .nombre-escenario:after {
  background-image: url("../img/icono_rock.png");
}

.calendario {
  padding: 0;
  list-style: none;
}
.calendario li {
  padding: 2.5rem;
  font-weight: 700;
}
.calendario li span {
  font-weight: 400;
}

.bg-verde .calendario li:nth-child(odd) {
  background-color: #3b9667;
}

.bg-amarillo .calendario li:nth-child(odd) {
  background-color: #fef03a;
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-imagenes {
  padding: 0;
  list-style: none;
  /* Mixin para hacer "tarjetas" de grid  con un número de columnas que nosotros mandemos */
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 5rem;
  /* Separación del grid */
}
@media (min-width: 768px) {
  .galeria-imagenes {
    /* Mixin para hacer "tarjetas" de grid  con un número de columnas que nosotros mandemos */
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2.5rem;
    /* Separación del grid */
  }
}
@media (min-width: 1200px) {
  .galeria-imagenes {
    /* Mixin para hacer "tarjetas" de grid  con un número de columnas que nosotros mandemos */
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    gap: 2.5rem;
    /* Separación del grid */
  }
}

.overlay {
  position: fixed;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  top: 0;
  display: grid;
  place-items: center;
}

.btn-cerrar {
  width: 3rem;
  height: 3rem;
  background-color: #fff;
  display: grid;
  place-items: center;
  font-weight: 900;
  font-size: 2rem;
  border-radius: 50%;
}
.btn-cerrar:hover {
  cursor: pointer;
}

.boletos {
  padding: 5rem 0;
}
.boletos h3 {
  text-align: center;
  color: #fff;
}
.boletos .contenedor-boletos {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .boletos .contenedor-boletos {
    /* Mixin para hacer "tarjetas" de grid  con un número de columnas que nosotros mandemos */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 5rem;
    /* Separación del grid */
  }
}
.boletos .plan p {
  color: #fff;
  font-size: 3rem;
  text-align: center;
  margin-bottom: 2rem;
}

.pase {
  width: 100%;
  border-spacing: 0;
}
.pase thead {
  background-color: #f53756;
}
.pase thead th {
  color: #fff;
  padding: 1.5rem;
  text-transform: uppercase;
  font-weight: 300;
  font-size: 2rem;
}
.pase tbody {
  background-color: #fff;
}
.pase tbody td {
  padding: 1.5rem;
  border-bottom: 1px solid #e1e1e1;
}
.pase tbody tr:last-of-type td {
  border: none;
}
.pase tfoot {
  background-color: #752f97;
}
.pase tfoot td {
  padding: 2rem;
  color: #fff;
  text-align: center;
  font-weight: 700;
  font-size: 2rem;
}

.footer {
  background-color: #f53756;
  padding: 1.5rem;
  text-align: center;
}
.footer p {
  font-size: 1.5rem;
  color: #fff;
  margin: 0;
}

/* Recordar que es importante el orden de como declaramos los imports */