.header {
  background-color: $verde;

  .contenido-header {                /* En SASS se pone el contenido dentro del header porque forma parte de él */
    padding: $separacion / 2 0;
    @include tablet {             /* Llamar al mixin */
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }

  h1 {
    text-align: center;
    color: $blanco;
    @include tablet {
      margin-bottom: 0;
    }
   
  }

  &.fijo {                        /* Cuando el header tenga la clase .fijo también... */
    position: fixed;
    width: 100%;                  /* Que tome todo el espacio disponible */
    box-shadow: 0 0.3rem 0.4rem #444;  /* Toma los sig valores para tener una sombra: sombra en el eje de las x, sombra en las y, radio de la sombra, color*/
  }
}

.navegacion-principal {
  display: flex;                /* Para agregar separación */ 
  gap: 2rem;                    /* De 2 rem, le ponemos un display de flex y el gap */
  justify-content: center;
  a {
    color: $blanco;
    font-size: 2rem;
    &:hover{                    /* Para que, al pasar el mouse por los enlaces, cambie a amarillo */ 
      color: $amarillo;         /* El & es para hacer referencia al enlace "a" */
    }
  }
}

