.boletos {
  padding: $separacion 0;

  h3 {
    text-align: center;
    color: $blanco;
  }

  .contenedor-boletos {
    margin-top: 2rem;
    @include tablet(){
      @include grid(2,5rem);
    }
}

  .plan{
    p {
      color: $blanco;
      font-size: 3rem;
      text-align: center;
      margin-bottom: 2rem;

    }
  }
}

.pase {
  width: 100%;
  border-spacing: 0;                // Quitar espacios entre elementos de la tabla
  thead {
    background-color: $rosa;
    th {
      color: $blanco;
      padding: 1.5rem;
      text-transform: uppercase;
      font-weight: $delgada;
      font-size: 2rem;
    }
  }

  tbody {
    background-color: $blanco;
    td {
      padding: 1.5rem;
      border-bottom: 1px solid #e1e1e1;     // Añadiendo borde inferior
    }

    tr {
      &:last-of-type {                      // Nos vamos al último elemento de tr
        td {
          border: none;                     // Quitamos el borde del último td
        }
      }
    }
  }

  tfoot {
    background-color: $morado;
    td {
      padding: 2rem;
      color: $blanco;
      text-align: center;
      font-weight: $bold;
      font-size: 2rem;
    }
  }
}
